.listItem {
  width: 150px;
  background-color: rgb(240, 240, 240);
  padding: 5px 5px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  box-shadow: 0 10px 10px -12px;
}

.listItem:not(:last-child) {
  margin-bottom: 20px;
}

.avatar {
  border-radius: 5px;
  margin-right: 4px;
}
